.App{ 
    height: auto;
    header{
        width: 100vw;
    }
    .start{
        width: 100vw;
        height: 100vh;
    }
    
    main{
        display: flex;
        /* background-color: #9595FF; */
        position: relative;
        width: auto;
        height: auto;
        box-sizing: border-box;
        overflow: hidden;
        .container{
            background-image: linear-gradient(#9595FF, #5651FF);
            
            width: 100vw;
            height: 100vh;
            max-height: 1920px;
            /* min-height: 600px; */
            img{
                position: absolute;
                filter: contrast(1.2);
            }
            .treeLeaves{
                z-index: 20;  
                height: 48%;    
            }
            .leavesRight{
                z-index: 20;
                right: 0;
                height: 29%;
            }
            .leavesLeftR, .leavesRightR, .trunksLeftR, .trunkRightR, .grassRightR, .grassLeftR, .grassMidBackR, .grassMidR, .vine1R{
                display: none;
            }
            //Troncos            
            .treeTrunks{
                z-index: 10;               
                height: 84%;   
                left: 20px;
                bottom: -5px;          
            }         
            .groupTrunks{
                position: absolute;
                width: 156vh;
                min-width: 936px;
                height: 100%;
                left: 0;
                .trunksContainer{
                    position: absolute;
                    right: 0;
                    width: 57%;
                    height: 100%;
                    .midTrunk{
                        z-index: 14;
                        height: 77%;
                        left: 0;
                        bottom: -1%;
                    }
                    .lastTrunk{
                        z-index: 12;
                        height: 77%;
                        left: 72%;
                        bottom: -1%;
                    }
                }              
            }  
            //Moitas
            .bushLeft{
                z-index: 12;
                bottom: 0;
                left: 0;
                height: 33%;
            }
            .bushRight{
                z-index: 12;
                bottom: 0;
                right: 0;
                height: 35.1%;
            }
            //Grama
            .leftGrass{
                z-index: 20;
                left: -16px;
                bottom: -2%;
                height: 19%;
            }
            .grassRight{
                z-index: 20;
                right: 0;
                bottom: -15px;
                height: 14.5%;
            }
            
            //Cipos
            .vine1{
                z-index: 14;
                height: 16%;
                top: 22%;
                left: 0;
            }
            .vine2{
                z-index: 14;
                height: 21%;
                top: 19%;
                left: 6%;
            }
            .vine3{
                z-index: 13;
                height: 12%;
                top: 28%;
                left: 62vh;
            }

            .moon{
                position: absolute;
                top: 20px;
                right: 53px;
                width: 250px;
                height: 250px;
                border-radius: 50%;
                /* background-color: red; */
                background-image: radial-gradient(#a0a0ff, rgba(255, 255, 255,  .84));
                box-shadow: 0 0 100px rgb(255, 255, 255);
            }
            .groupMark{
                height: 100%;
                width: auto;
                position: absolute;
                bottom: 0;
                left: 190px;
                .branchTrunkR{
                    display: none;
                }
                .branchTrunk{               
                    z-index: 15;
                    /* width: 385px; */
                    height: 75.5%;
                    left: -13%;
                    bottom: 0;
                }
                svg{
                    z-index: 18;       
                    position: absolute;     
                    height: 60%;
                    left: -8%;
                    top: 18%;                
                    g{
                        path{
                            filter: drop-shadow(0 0 2.75rem #ffffff);
                            fill:#fff;
                            /* animation: pisca .2s infinite; */
                        }
                        rect{
                            fill: none;
                        }
                    }
                }
                
                .circleCoruja{
                    z-index: 14;        
                    position: relative;       
                    left: -7%;
                    top: 14%;
                    min-width: 386px;
                    min-height: 383px;
                    width: 60vh;                   
                    height: 60vh;
                    border-radius: 50%;
                    border: 1vh solid #fff;
                    filter: drop-shadow(0 0 1.75rem #ffffff);
                   /*  animation: pisca .2s infinite; */
                }
            }

            h1{ 
                z-index: 13;
                bottom: 8%;
                left: .7%;
                position: absolute;
                color: #fff;
                font-size: 18vh;
                font-family: 'Open Sans', sans-serif;
                
            }    
            .conversionContainer{
                position: absolute;
                z-index: 20;
                display: flex;
                right: 0;
                width: 46%;
                height: 100%;
                align-items: center;
                .conversion{
                    width: 839px;
                    height: auto;
                    h2{
                        font-family: 'SoleilBold', sans-serif;
                        color: #fff;
                        font-size: 4rem;
                        right: 0;
                        text-shadow: 0 0 20px #00000026;
                    }
                    h3{
                        font-family: 'SoleilRegular', sans-serif;
                        color: #fff;
                        max-width: 691px;
                        font-size: 1.2rem;
                        margin-top: 20px
                    }
                    .openContact{
                        @include button();
                        margin-top: 20px;
                        width: 305px;
                        height: 60px;
                        background-image: linear-gradient(45deg, #ff877d, #ffb58a);
                        box-shadow: 0 8px 30px rgba(255, 135, 125, .4);
                        border-radius: 100px;
                        font-family: 'SoleilBook', sans-serif;
                        color: #fff;                       
                        font-size: 1.85rem;
                        transition: .3s ease-out;
                        &:hover{
                            transform: scale(1.07);
                            box-shadow: 0 16px 40px rgba(255, 135, 125, .6);
                        }
                        &:active{
                            transform: scale(.95);
                            box-shadow: 0 8px 30px rgba(255, 135, 125, .4);
                        }
                    }
                } 
            }  
        }      
    }
    .achievements{
        position: absolute;
        z-index: 100;
        left: 50%;
        transform: translateX(-50%);
        top: 92vh;
        width: 900px;
        height: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-radius: 16px;
        background-color: rgba(255, 255, 255, .18);
        backdrop-filter: blur(30px);
        padding: 15px 14px 11px 11px;
        box-sizing: border-box;
        /* box-shadow: 0 8px 24px rgba(255, 255, 255, .24); */
        p{
            color: #fff;
            font-family: 'Raleway-Medium';
            text-align: center;
            font-size: 1.1rem;
        }

        div{
            width: 155px;
            height: 114px;
            div{
                height: 78% !important;
                g{
                    path{
                        fill: #fff;
                        stroke: #fff;
                    }
                }
            }
        }
    } 
    .motivacao{
        width: 100vw;
        height: auto;
        .grainBg{
            width: 100%;
            height: 900px;
            max-height: auto;
            box-sizing: border-box;
            overflow: hidden;
            background-image: url(../../../assets/images/motivacao/grainBg.webp);
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            position: relative;
            img{
                position: absolute;
            }
            .purpleBall{
                left: 50%;
                transform: translateX(-50%);
                top: -570px;
            }
            .blueBall{
                top: 50%;
                transform: translateY(-50%);
                left: -740px;
            }
            .orangeBall{
                right: -630px;
                bottom: -285px;
            }
            .grainOpacityBg{
                width: 100%;
                height: 100%;
                background-image: url(../../../assets/images/motivacao/grainOpacity.webp);
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
                img{
                    position: absolute;
                    z-index: 99;
                }                
                .tugleLeft{
                    left: -358px;
                    bottom: -413px;
                    transform: rotate(320deg);
                    z-index: 90;
                }
                .tugleRight{
                    z-index: 90;
                    right: -293px;
                    top: -331px;
                    transform: rotate(60deg);
                }
                .layerLine{
                    width: 100%;
                    height: 100%;
                    z-index: 95;
                    position: absolute;
                    display: flex;
                    justify-content: space-between;
                    box-sizing: border-box;
                    padding: 0 10%;
                    @mixin line {
                        width: 1.5px;
                        height: 100%;
                        opacity: 35%;
                        background-image: linear-gradient(rgb(255, 255, 255), rgba(255, 255, 255, .26));                    
                    }
                    .line1{
                        @include line();
                    }
                    .line2{
                        @include line();
                    }
                    .line3{
                        @include line();
                    }
                    .line4{
                        @include line();
                    }
                }
                .content{
                    position: absolute;
                    z-index: 95;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    .textContainer{
                        /* max-width: 50%; */
                        width: 700px;
                        margin-left: 50px;
                        h3{
                            font-family: 'newOrderBold', sans-serif;
                            color: #ffffff;
                            font-size: 3.5rem;
                            line-height: 1.45;
                            span{
                                color: #809bff;
                            }
                        }
                        p{
                            font-family: 'newOrderRegular', sans-serif;
                            color: #fff;
                            line-height: 1.6;
                            width: 500px;
                            font-size: 1.3rem;
                        }
                    }
                    .imgContainer{
                        display: flex;
                        width: calc(100% - 700px);
                        height: 100%;
                        align-items: center;
                        justify-content: flex-end;
                        .contectionContainer{
                            width: 1200px;
                            height: 100%;
                            position: relative;
                            div{
                                position: absolute;
                                right: 0;
                            }                         
                        }
                    }
                }
            }
        }
    }
}
@keyframes pisca {
    to{
        /* filter: brightness(20%); */
        filter: drop-shadow(0);
        fill: #172857;
        border-color: #172857;
    }
    from{
        filter: brightness(100%);
        filter: drop-shadow(0 0 1.75rem #ffffff);
        border-color: #fff;
        fill: #fff;
    }
}